import React from "react";
import './stats.css';

export function Stats() {

    return (
        <div className="food-stats-main">
            <div className="graph-one">
                <a href="https://www.statista.com/chart/24349/estimated-annual-global-food-waste-by-sector/" title="Infographic: Food Waste Is Becoming A Billion Tonne Problem | Statista"><img src="https://cdn.statcdn.com/Infographic/images/normal/24349.jpeg" alt="Infographic: Food Waste Is Becoming A Billion Tonne Problem | Statista" /></a>
            </div>
            <div className="graph-one-stats">
                <article>
                    <p>"A new report released by the United Nations Environment Programme has revealed the shocking scale of global food waste. Its 2021 Food Waste Index estimates that people discard 931 million tonnes of food annually with per capita food waste averaging 74kg per household.</p>
                    <p>569 million tonnes falls into the category of household waste. Supermarkets and other businesses are also guilty of disposing of signficant quanities of food with the total amount discarded also adding up to hundreds of millions of tonnes per year. The report estimates that 244 million tonnes is wasted by food service annually while retail discards 118 million tonnes.</p>
                    <p>UNEP defines food waste as food that completes the food supply chain up to a final product, of good quality and fit for consumption, but still doesn't get consumed because it is discarded, whether or not after it is left to spoil or expire. Food waste typically (but not exclusively) takes place at retail and consumption stages in the food supply chain."</p>
                    <p className="article-link">- Find out more at <a href="https://www.statista.com/chart/24349/estimated-annual-global-food-waste-by-sector/" target='_blank' rel="noreferrer">Statista</a></p>
                </article>
            </div>
            <div className="graph-two">
                <a href="https://www.statista.com/chart/15798/are-best-before-dates-past-their-best/" title="Infographic: How Inaccurate Are Expiration Dates? | Statista"><img src="https://cdn.statcdn.com/Infographic/images/normal/15798.jpeg" alt="Infographic: How Inaccurate Are Expiration Dates? | Statista" /></a> 
            </div>
            <div className="graph-two-stats">
                <article>
                    <p>"In the push to reduce food waste there are calls for significant changes to be made to the current system of expiration dates. Most people have experienced the doubt and confusion when faced with a 'best before' or 'sell by' date and in a lot of cases the solution to this confusion is to err on the side of caution and throw it away. To be more precise, 'a lot of cases' equated to (or at least contributed to) an estimated 340 million tons of food wasted after purchase globally in 2015.</p>
                    <p>An overhaul of the system of these dates to bring more clarity could help reduce this kind of food waste and according to guidelines set out by the German Food Bank (Tafel Deutschland), the average expiration date for some products is far too conservative, with foods such as pasta and rice often edible for a year after the date on the package. Even products such as eggs, with which a lot of care is usually exercised, consumers could expect them to be consumable for an extra 21 days."</p>
                    <p className="article-link">- Find out more at <a href="https://www.statista.com/chart/15798/are-best-before-dates-past-their-best/" target='_blank' rel="noreferrer">Statista</a></p>
                </article>
            </div>
            <div className="graph-three">
                <a href="https://www.statista.com/chart/19672/global-shares-of-different-agricultural-products-thrown-away/" title="Infographic: 14 Percent of Food Goes to Waste | Statista"><img src="https://cdn.statcdn.com/Infographic/images/normal/19672.jpeg" alt="Infographic: 14 Percent of Food Goes to Waste | Statista" /></a>
            </div>
            <div className="graph-three-stats">
                <article>
                    <p>"About 14 percent of the world's food is lost before it even reaches retail. This is the conclusion of a report released this week by the UN Food and Agriculture Organization (FAO). The losses are particularly high for roots, tubers and oil-bearing crops (25.3 percent), followed by fruits and vegetables (21.6 percent). These numbers do not take into account food thrown away by retailers or by consumers themselves.</p>
                    <p>According to the report, the losses occur due to incorrect harvesting times, climatic conditions, incorrect harvesting techniques, poor storage and improper transport. Countries in Central and South Asia are particularly affected by food losses (20.7 percent). But North America and Europe (15.7 percent) also lose food at a rate that is above the world average (13.8 percent). Regions savvier about food waste are Australia and New Zealand (5.8 percent), the rest of Oceania (8.9 percent) as well as Eastern and Southeast Asia (7.8 percent)."</p>
                    <p className="article-link">- Find out more at <a href="https://www.statista.com/chart/19672/global-shares-of-different-agricultural-products-thrown-away/" target='_blank' rel="noreferrer">Statista</a></p>
                </article>
            </div>
            <div className="graph-four">
            <a href="https://www.statista.com/chart/20825/food-waste-by-consumers-selected-countries/" title="Infographic: Food Waste Most Prevalent in Affluent Countries | Statista"><img src="https://cdn.statcdn.com/Infographic/images/normal/20825.jpeg" alt="Infographic: Food Waste Most Prevalent in Affluent Countries | Statista" /></a>
            </div>
            <div className="graph-four-stats">
                <article>
                    <p>"New research published in the journal Plos One suggests that consumers waste more food than previously thought. A team around The Hague-based researcher Monika van den Bos Verma compared food availability and daily calorie intake in different countries and found that the average world citizen wasted 727 calories per day. In the U.S., that number was a lot higher at 1,572 calories per day â the second on the list after Bermuda.</p>
                    <p>Additionally, one third of all food goes to waste before even reaching the market, according to the FAO. This is commonly known as food loss.</p>
                    <p>The researchers found that more affluent countries tend to throw out the most food, with Norway, Hong Kong and Germany all on the top of the list and developing countries wasting much smaller amounts. Out of the 177 countries that were part of the analysis, 40 were identified as not wasting any food per the study setup, since their food availability was below the daily caloric need of their inhabitants."</p>
                    <p className="article-link">- Find out more at <a href="https://www.statista.com/chart/20825/food-waste-by-consumers-selected-countries/" target='_blank' rel="noreferrer">Statista</a></p>
                </article>
            </div>
            <div className="other-links">
                <h3>For more info check out these resources:</h3>
                <ul>
                    <a href="https://www.ers.usda.gov/topics/food-nutrition-assistance/food-security-in-the-u-s/key-statistics-graphics/#insecure" target='_blank' rel="noreferrer"><li>USDA - Food Security in the U.S.</li></a>
                    <a href="https://www.fda.gov/food/consumers/food-loss-and-waste" target='_blank' rel="noreferrer"><li>FDA - Food Loss and Waste</li></a>
                    <a href="https://www.rts.com/resources/guides/food-waste-america/" target='_blank' rel="noreferrer"><li>rts - Food Waste in America in 2022</li></a>
                </ul>
            </div>
        </div>
    );
}